<template>
  <div class="container-fluid --general-stats text-center mb-4 pt-4">
    <div v-if="generalStats.data">
      <div class="row">
        <div class="col-sm-4 col-xs-12">
          <div class="card h-100 animated bounceInLeft">
            <div class="card-body">
              <div class="row">
                <div class="col-md-4 col-sm-12 d-none d-md-block">
                  <img src="@/assets/images/corona-confirmed.png" class="img-fluid" />
                </div>

                <div class="col-md-8 col-sm-12">
                  <p class="lead font-weight-bold mb-0 text-center text-md-left">
                    <span
                      class="number --general-stat-number text-warning"
                    >{{generalStats.data.confirmed | comma}}</span>
                    <br />Confirmed
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div class="d-sm-block d-md-none mb-2"></div>
        </div>

        <div class="col-sm-4 col-xs-12">
          <div class="card h-100 animated bounceIn">
            <div class="card-body">
              <div class="row">
                <div class="col-md-4 col-sm-12 d-none d-md-block">
                  <img src="@/assets/images/corona-death.png" class="img-fluid" />
                </div>
                <div class="col-md-8 col-sm-12">
                  <p class="lead font-weight-bold mb-0 text-center text-md-left">
                    <span
                      class="number --general-stat-number text-danger"
                    >{{generalStats.data.deaths | comma}}</span>
                    <br />Deaths
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div class="d-sm-block d-md-none mb-2"></div>
        </div>

        <div class="col-sm-4 col-xs-12">
          <div class="card h-100 animated bounceInRight">
            <div class="card-body">
              <div class="row">
                <div class="col-md-4 col-sm-12 d-none d-md-block">
                  <img src="@/assets/images/corona-safe.png" class="img-fluid" />
                </div>

                <div class="col-md-8 col-sm-12">
                  <p class="lead font-weight-bold mb-0 text-center text-md-left">
                    <span
                      class="number --general-stat-number text-success"
                    >{{generalStats.data.safe | comma}}</span>
                    <br />Are Safe
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div class="d-sm-block d-md-none mb-2"></div>
        </div>
      </div>
      <div class="row">
        <div class="col-12">
          <div class="progress">
            <div
              class="progress-bar bg-warning"
              role="progressbar"
              :style="'width:' + statPercent.confirmed + '%'"
            ></div>
            <div
              class="progress-bar bg-danger"
              role="progressbar"
              :style="'width:' + statPercent.deaths + '%'"
            ></div>
          </div>
        </div>
      </div>
    </div>
    <template v-if="generalStats.loading">
      <Loading message="Fetching data..." />
    </template>
  </div>
</template>

<script>
import Loading from "@/components/Loading.vue";

export default {
  name: "GeneralStats",
  props: {
    generalStats: Object
  },
  components: { Loading },
  filters: {
    comma(value) {
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
  },
  computed: {
    statPercent() {
      let total =
        this.generalStats.data.confirmed + this.generalStats.data.deaths;
      return {
        confirmed: (this.generalStats.data.confirmed / total) * 100,
        deaths: (this.generalStats.data.deaths / total) * 100
      };
    }
  }
};
</script>

